import React from "react";
import { Heading, Image, Text } from "@chakra-ui/react";
import "./aboutme.css";
import { Divider } from "@chakra-ui/react";
import Typewriter from "typewriter-effect";
import { useMediaQuery } from "@chakra-ui/react";
const AboutMe = () => {
	const [isLargerThan800] = useMediaQuery("(min-width: 700px)");
	return (
		<div className="abt-me" id="about">
			<div className="white-grad" />
			<Heading className="hline" p={"30"} fontFamily={"Roboto Mono"}>
				<Typewriter
					options={{
						strings: ["< AboutMe />"],
						autoStart: true,
						loop: true,
					}}
				/>
			</Heading>
			{isLargerThan800 ? (
				<div className="detail">
					<div className="abt-img">
						<Image
							className="img"
							borderRadius="20px"
							boxSize="300px"
							src="https://bit.ly/dan-abramov"
							alt="Dan Abramov"
						/>
					</div>
					<div className="abt-content">
						<Heading
							fontFamily={"Roboto Mono"}
							color={"white"}
							lineHeight={"200%"}
							textAlign={"left"}>
							Hey There! I'm Sarthak Ahuja
						</Heading>
						<Text color={"white"} textAlign={"left"}>
							I am a Bootcamp graduate from Masai School,Bengaluru.I am a highly
							passionate Full stack web developer with considerable technical
							skills who is passionate about coding and designing a website
							using HTML, CSS, JavaScript, React Js, Redux, MongoDB, and Chakra
							UI. And possess self- discipline and the ability to work with
							minimum supervision. Looking forward to joining a progressive
							organization with opportunities to work on significant projects.
						</Text>
					</div>
				</div>
			) : null}
			<Divider height="100px" width="50%" m="auto" orientation="horizontal" />
		</div>
	);
};

export default AboutMe;
